import React, { useState } from 'react';
import Table from  '../Components/Table';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import ProfileCard from '../Components/ProfileCard';
import axios from 'axios';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import SwipeableViews from 'react-swipeable-views';
import Alert from '../Components/Alert'
import { useParams, useRouteMatch } from 'react-router';




TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'inline-block',
    marginTop: '50px',
    marginLeft: '50px',
    verticalAlign: 'top',
    border: "3px solid green",
  },
  paper: {
    marginRight: theme.spacing(10),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;


  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

export default function HomeAsd(props) {
  const classes = useStyles();
  let [unfollowers, setUnfollowers] = useState();
  let [fans, setFans] = useState();
  let [followers, setFollowers] = useState();
  let [followings, setFollowings] = useState();

  let [username, setUsername] = useState();
  let [fullName, setFullName] = useState();
  let [biography, setBiography] = useState();
  let [profilePicUrl, setProfilePicUrl] = useState();
  let [mediaCount, setMediaCount] = useState();
  let [isPrivate, setIsPrivate] = useState();
  let [pk, setPk] = useState();

  let [alertMessage, setAlertMessage] = useState();
  let [alertSeverity, setAlertSeverity] = useState();

  let [openAlert, setOpenAlert] = useState(false)
  let [open, setOpen] = useState(true)
  const [value, setValue] = React.useState(0);
  const theme = useTheme();

  const { path } = useRouteMatch() || {};
  const { id } = useParams();

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const handleChangeIndex = (index) => {
    setValue(index);
  };
  React.useEffect(() => {
    id && console.log(id)
    setOpen(true)
    console.log(JSON.parse(localStorage.getItem('usernameId')))
    if (JSON.parse(localStorage.getItem('username')) === props.username && JSON.parse(sessionStorage.getItem('unfollowers'))){
      setFollowers(JSON.parse(sessionStorage.getItem('followers')))
      setFollowings(JSON.parse(sessionStorage.getItem('followings')))
      setUnfollowers(JSON.parse(sessionStorage.getItem('unfollowers')))
      setFans(JSON.parse(sessionStorage.getItem('fans')))

      setUsername(JSON.parse(sessionStorage.getItem('username')))
      setFullName(JSON.parse(sessionStorage.getItem('fullName')))
      setBiography(JSON.parse(sessionStorage.getItem('biography')))
      setProfilePicUrl(JSON.parse(sessionStorage.getItem('profilePicUrl')))
      setMediaCount(JSON.parse(sessionStorage.getItem('mediaCount')))
      setIsPrivate(JSON.parse(sessionStorage.getItem('private')))
      setPk(JSON.parse(sessionStorage.getItem('pk')))

      setOpen(false);
    }else{
      let name = id? id: props.username
      console.log(id)
      console.log(props.username)
      axios.get(`https://instagram-server-api.herokuapp.com/profile/`+name,
      {headers:{
        "username_id": JSON.parse(localStorage.getItem('usernameId')),
        "token": JSON.parse(localStorage.getItem('token')),
        "rank_token": JSON.parse(localStorage.getItem('rankToken')),
        "uuid": JSON.parse(localStorage.getItem('uuid')),
        "sessionid": JSON.parse(localStorage.getItem('sessionid'))
      }})
      .then(res => {
        setUnfollowers(res.data.unfollowers);
        setFans(res.data.fans);
        setFollowers(res.data.followers);
        setFollowings(res.data.followings);

        setUsername(res.data.user_info.username);
        setFullName(res.data.user_info.full_name);
        setBiography(res.data.user_info.biography);
        setProfilePicUrl(res.data.user_info.profile_pic_url);
        setMediaCount(res.data.user_info.media_count);
        setIsPrivate(res.data.user_info.is_private);
        setPk(res.data.user_info.pk);

        if (JSON.parse(localStorage.getItem('username')) === name){
        sessionStorage.setItem('unfollowers',JSON.stringify(res.data.unfollowers));
        sessionStorage.setItem('fans',JSON.stringify(res.data.fans));
        sessionStorage.setItem('followers',JSON.stringify(res.data.followers));
        sessionStorage.setItem('followings',JSON.stringify(res.data.followings));

        sessionStorage.setItem('username',JSON.stringify(res.data.user_info.username));
        sessionStorage.setItem('fullName',JSON.stringify(res.data.user_info.full_name));
        sessionStorage.setItem('biography',JSON.stringify(res.data.user_info.biography));
        sessionStorage.setItem('profilePicUrl',JSON.stringify(res.data.user_info.profile_pic_url));
        sessionStorage.setItem('mediaCount',JSON.stringify(res.data.user_info.media_count));
        sessionStorage.setItem('isPrivate',JSON.stringify(res.data.user_info.is_private));
        sessionStorage.setItem('pk',JSON.stringify(res.data.user_info.pk));
        
        console.log(sessionStorage.getItem('biography'))
        }
        setOpen(false);
    }).catch((error) => {
      setAlertMessage("Error "+ error.response.status + ": " + error.response.data.message)
      setAlertSeverity("error")
      setOpen(false)
      setOpenAlert(true)
    })
   
  }}, [id]);


  return (
    <div>
        <Alert 
          handleClose={()=> setOpenAlert(false)} 
          open = {openAlert} 
          alertSeverity = {alertSeverity}
          alertMessage = {alertMessage}
        />

        <div>
          <Backdrop className={classes.backdrop} open={open}>
            <CircularProgress color="inherit" />
          </Backdrop>
        </div>
        {!open && followings &&
        <div style={{margin: "0 auto"}}>
          <ProfileCard 
            username = {username}
            fullName = {fullName}
            biography = {biography}
            mediaCount= {mediaCount}
            profilePicUrl = {profilePicUrl} 
            followers={followers} 
            followings={followings}/>
        
          <div style={{marginTop:"75px"}}>
          <AppBar position="static" color="default">
            <Tabs
              value={value}
              onChange={handleChange}
              indicatorColor="primary"
              textColor="primary"
              variant="fullWidth"
              aria-label="full width tabs example"
            >
              <Tab label="Followers" {...a11yProps(0)} />
              <Tab label="Followings" {...a11yProps(1)} />
              <Tab label="Non-Followers" {...a11yProps(2)} />
              <Tab label="Non-Followings" {...a11yProps(3)} />
            </Tabs>
          </AppBar>
          <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={value}
            onChangeIndex={handleChangeIndex}
          >
            <TabPanel value={value} index={0} dir={theme.direction}>
              <Table asd = {followers}/>
            </TabPanel>
            <TabPanel value={value} index={1} dir={theme.direction}>
              <Table asd = {followings}/>
            </TabPanel>
            <TabPanel value={value} index={2} dir={theme.direction}>
              <Table asd = {unfollowers}/>
            </TabPanel>
            <TabPanel value={value} index={3} dir={theme.direction}>
              <Table asd = {fans}/>
            </TabPanel>
            </SwipeableViews>
          </div>
          
          
        </div>}
        
    </div>
  );
}