import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import '../Images/verified.jpg';
import LockIcon from '@material-ui/icons/Lock';
import {useHistory} from "react-router";





const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    maxHeight: 1000,
  },
  img: {
    borderRadius: '50%',
    height: '50px',
    width: '50px',
  },
  verified: {
    borderRadius: '50%',
    maxHeight: '20px',
    maxWidth: '20px',
    display: 'inline-block',
    verticalAlign: 'middle',
    marginLeft: '5px',
  },
  private: {
    marginRight:'5px',
    color: 'grey',
    verticalAlign: 'middle',
  }
});

export default function StickyHeadTable(props) {
  const classes = useStyles();
  const history = useHistory();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader  size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell align='left' style={{width:'10px'}}>
                <b>Foto</b>
              </TableCell>
              <TableCell align='left'>
                <b>Username</b>
              </TableCell>
              
            </TableRow>
          </TableHead>
          <TableBody>
            {props.asd.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
              return (
                <TableRow hover role="checkbox" tabIndex={-1} key={row.code} onClick={()=>history.push('/profile/'+row.username)}>
                  <TableCell key='pic' align='left'>
                    <img src={row.profile_pic_url} alt="Profile pic" className = {classes.img}></img>
                  </TableCell>
                  <TableCell key='pic' align='left' style={{verticalAlign:'center'}}>
                    <div>
                      {row.is_private? <LockIcon className={classes.private}/>:null}
                      <span style={{display:"inline-block", verticalAlign:'middle'}}>{row.username}</span>
                      {row.is_verified? <img src={require('../Images/verified.jpg')}className = {classes.verified} alt="Verified"></img>:null}
                    </div>
                  </TableCell>

                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10, 15]}
        component="div"
        count={props.asd.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </Paper>
  );
}