import React from 'react';
import './App.css';
import Menu from './Components/Menu'
import Paper from '@material-ui/core/Paper';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from './Screens/Home';
import Login from './Screens/Login';
import { Redirect, useParams, useRouteMatch } from 'react-router';



function App() {
  // // const { path } = useRouteMatch() || {};
  // const { id } = useParams();
  return (
    <div className="App">
      <Router>
        <div style={{textAlign:"left", overflow:"hidden", margin: "0 auto"}}>
        <Menu />
        <Paper style={{overflow: "hidden", display: "inline-block",  verticalAlign: 'top', width: '800px' , marginTop:"50px"}}>
          <Switch>
            <PrivateRoute path="/usuarios" component={props => <Home />} />
            <PrivateRoute exact path="/profile" component={props => <Home username={JSON.parse(localStorage.getItem('username'))}/>} />
            <PrivateRoute exact path={`/profile/:id`} component={props => <Home username={null}/>} />
            <Route exact path="/login" component={props => <Login/>} />
            <Redirect from='/' exact={true}to='/profile' />
            <Route path='*' exact={true} render={()=><div>Not found</div>} />
          </Switch>
        </Paper>
        </div>
      </Router>

    </div>
  );
}

export default App;


const PrivateRoute = ({ component: Component, ...rest}) => (
  <Route 
      {...rest}
      render={props =>
          localStorage.getItem("sessionid") ? (
              <Component {...props} />
          ) : (
              <Redirect
                  to={{
                      pathname: "/login",
                      state: { from: props.location }
                  }}
              />
          )
      }
  />
)