import React , { useState } from 'react';
import {
  ThemeProvider,
  makeStyles,
  createMuiTheme,
} from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import {useHistory} from "react-router";
import CircularProgress from '@material-ui/core/CircularProgress';
import Alert from '../Components/Alert'

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'block',
    flexWrap: 'wrap',
    justifyContent: "center"
    // textAlign: 'center'
  },
  margin: {
    margin: theme.spacing(1),
    display: 'flex',
    width:'250px',
    marginLeft:'auto',
    marginRight:'auto',
    // textAlign: 'center'
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
  button: {
    margin: theme.spacing(1),
    display: 'flex',
    width:'250px',
    marginLeft:'auto',
    marginRight:'auto',
    marginTop: '20px',
  },
  wrapper: {
    margin: theme.spacing(1),
    position: 'relative',
  },
  img: {
    display: 'flex',
    width:'250px',
    marginLeft:'auto',
    marginRight:'auto',
  },
  forgotPassword:{
    marginTop: '50px',
    textAlign: 'center',
    position: 'absolute',
    top: '50%',
    left: '50%',
  },
}));

const theme = createMuiTheme({
  palette: {
    primary: green,
  },
});

export default function CustomizedInputs() {
  const classes = useStyles();
  const history = useHistory();
  let [username, setUsername] = useState();
  let [password, setPassword] = useState();
  let [loading, setLoading] = useState(false);
  let [alertMessage, setAlertMessage] = useState();
  let [alertSeverity, setAlertSeverity] = useState();
  let [openAlert, setOpenAlert] = useState(false)


  const handleChangePassword = (e) => {
    setPassword(e.target.value);
  }
  const handleChangeUsername = (e) => {
    setUsername(e.target.value);
  }
  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true)
    axios.post('https://instagram-server-api.herokuapp.com/login', {}, {headers: {"username":username,"password":password}})
      .then(res => {
        if (res.status === 200) {
            localStorage.setItem('token',JSON.stringify(res.data.token));
            localStorage.setItem('sessionid',JSON.stringify(res.data.sessionid));
            localStorage.setItem('rankToken',JSON.stringify(res.data.rank_token));
            localStorage.setItem('uuid',JSON.stringify(res.data.uuid));
            localStorage.setItem('usernameId',JSON.stringify(res.data.username_id));
            localStorage.setItem('username',JSON.stringify(res.data.username));
            history.push('/profile')
        }
        else{
            setLoading(false)
            console.log("Error en login")
        }
        
      }).catch((error) =>{
        setAlertMessage("Error "+ error.response.status + ": " + error.response.data.message)
        setAlertSeverity("error")
        setOpenAlert(true)
        setLoading(false)
      })
  }
  React.useEffect(() => {
      if (localStorage.getItem('sessionid')){
          history.push('/profile')
      }
    
  },[]);
  return (
    <form className={classes.root} noValidate>
      <Alert 
          handleClose={()=> setOpenAlert(false)} 
          open = {openAlert} 
          alertSeverity = {alertSeverity}
          alertMessage = {alertMessage}
        />
        <img src={require('../Images/instagramCartel.png')} alt="Profile pic" className = {classes.img}></img>
      <ThemeProvider theme={theme}>
        <TextField
          className={classes.margin}
          inputProps={{min: 0, style: { textAlign: 'center' }}}
          label="Username"
          variant="outlined"
          id="mui-theme-provider-outlined-input"
          onChange= {handleChangeUsername}
        />
        <TextField
          className={classes.margin}
          inputProps={{min: 0, style: { textAlign: 'center' }}}
          label="Password"
          type='password'
          variant="outlined"
          id="mui-theme-provider-outlined-input"
          
          onChange= {handleChangePassword}
        />
      </ThemeProvider>
      <div className={classes.wrapper}>
        <Button
          variant="contained"
          color="primary"
          disabled={loading}
          type='submit'
          className={classes.button}
          onClick={handleSubmit}
        >
          Login
        </Button>
        {loading && <CircularProgress size={24} className={classes.buttonProgress} />}

        <a className={classes.forgotPassword} href="http://instagram.com/accounts/password/reset/" tabindex="0">¿Olvidaste tu contraseña?</a>
      </div>
      
    </form>
  );
}